import { createRouter, createWebHashHistory } from 'vue-router'
import Naslovna from '../views/Naslovna.vue'
import OSajmu from '../views/OSajmu.vue'
import Kompanije from '../views/Kompanije.vue'
import Organizatori from '../views/Organizatori.vue'
import Standovi from '../views/Standovi.vue'
import Brosura from '../views/Brosura.vue'
import TechTalk from '../views/TechTalk.vue'
import InfoPult from '../views/InfoPult.vue'
import CVCorner from '../views/CVCorner.vue'
import Prijava from '../views/Prijava.vue'
import Registracija from '../views/Registracija.vue'

const routes = [
  {
    path: '/',
    name: 'Naslovna',
    component: Naslovna
  },
  {
    path: '/o-sajmu',
    name: 'OSajmu',
    component: OSajmu
  },
  {
    path: '/kompanije-ucesnice',
    name: 'Kompanije',
    component: Kompanije
  },
  {
    path: '/organizatori',
    name: 'Organizatori',
    component: Organizatori
  },
  {
    path: '/standovi',
    name: 'Standovi',
    component: Standovi
  },
  {
    path: '/brosura',
    name: 'Brosura',
    component: Brosura
  },
  {
    path: '/tech-talk',
    name: 'TechTalk',
    component: TechTalk
  },
  {
    path: '/info-pult',
    name: 'InfoPult',
    component: InfoPult
  },
  {
    path: '/cv-corner',
    name: 'CVCorner',
    component: CVCorner
  },
  {
    path: '/prijava',
    name: 'Prijava',
    component: Prijava
  },
  {
    path: '/registracija',
    name: 'Registracija',
    component: Registracija
  },
  {
    path: '/:catchAll(.*)',
    name: 'NemaMe',
    component: Naslovna
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
