<template>
    <router-link :to="href">
        <li>
            <InlineSvg :src="publicPath + svg" /> 
            {{ text }}
        </li>
    </router-link>
</template>

<script>
import InlineSvg from 'vue-inline-svg'

export default {
    components: {
        InlineSvg
    },
    props: {
        svg: String,
        text: String,
        href: String
    },
    data () {
        return {
            publicPath: process.env.BASE_URL
        }
    }
}

</script>

<style scoped>
    li {
        display: flex;
        align-items: center;
        border-right: transparent 4px solid;
        font-size: 20px;
        font-weight: 400;
        color: rgb(66, 66, 66);
        margin-top: 20px;
    }

    li:hover, .router-link-active li {
        background-color: #eee;
        border-right: var(--orange) 4px solid;
        color: var(--orange);
        box-shadow: 0 3px 3px rgba(0, 0, 0, 0.15);
        cursor: pointer;
    }

    li svg {
        height: 30px;
        padding: 5px 20px;
        margin-right: 30px;
    }
</style>