<template>
    <router-link :to="href">
        <button :class="color || 'btn-green'">
            <InlineSvg :src="publicPath + svg" />
            {{ text }}
        </button>
    </router-link>
</template>

<script>
import InlineSvg from 'vue-inline-svg'

export default {
    name: 'Button',
    props: {
        svg: String,
        href: String,
        text: String,
        color: String
    },
    components: {
        InlineSvg
    },
    data () {
        return {
            publicPath: process.env.BASE_URL
        }
    }
}
</script>

<style>
    button {
        display: flex;
        color: white;
        border: none;
        padding: 10px;
        border-radius: 5px;
        box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.15);
        cursor: pointer;
    }

    button svg {
        height: 15px;
        margin-right: 5px;
    }

    .btn-green {
        background: var(--green-gradient-h);
    }

    .btn-orange {
        background: var(--orange-gradient-h);
    }
</style>